              <v-card-text style="padding-top: 0; padding-bottom: 0">
                <div class="center-tab">
                  <div v-if="signUpStatus == 0" class="simulate-tab">
                    <i class="el-icon-share large"></i>
                    <br />COMPARTIR CON
                  </div>
                  <!-- COMPARTIR -->

                  <div v-if="signUpStatus == 1" class="simulate-tab">
                    <i class="el-icon-question large"></i>
                    <br />INFORMES
                  </div>

                  <div v-if="signUpStatus == 3" class="simulate-tab">
                    <i class="el-icon-share large"></i>
                    <br />COMPARTIR CON
                  </div>
                  <!-- COMPARTIR TODO -->
                </div>

                <v-container style="padding-top: 0; padding-bottom: 0">
                  <v-layout wrap>
                    <v-flex xs12>
                      <v-text-field
                        color="#13D8F2"
                        label="Nombre"
                        :rules="[rules.required]"
                        v-model="name"
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        color="#13D8F2"
