                        v-model="phone"
                        :rules="[rules.required, rules.number]"
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        color="#13D8F2"
                        label="Correo electrónico"
                        type="email"
                        :rules="[rules.required, rules.email]"
                        v-model="email"
                      ></v-text-field>
                    </v-flex>

                    <v-flex xs12 v-if="signUpStatus == 1 || signUpStatus == 0">
                      <v-textarea
                        color="#13D8F2"
                        label="Mensaje"
                        type="message"
                        :rules="[rules.required]"
                        v-model="messageInfo"
                        rows="4"
                      ></v-textarea>
                    </v-flex>

                    <v-flex xs12 v-if="signUpStatus == 2">
                      <v-text-field
                        color="#13D8F2"
                        label="Contraseña"
                        :type="show ? 'text' : 'password'"
                        :append-icon="show ? 'visibility' : 'visibility_off'"
                        @click:append="show = !show"
                        :rules="[rules.required]"
                        v-model="password"
                      ></v-text-field>
                    </v-flex>
                  </v-layout>

                  <div v-if="signUpStatus == 1">
                    <v-alert
                      v-model="alert"
                      dismissible
                      transition="scale-transition"
                      style="color: #13D8F2; background:#fff !important"
                    >{{ message }}</v-alert>
                    <v-btn
                      class="btn-align-left"
                      block
                      :disabled="sending"
                      color="#13D8F2"
                      @click="send(), infoEmail()"
                    >
                      <v-icon right dark>email</v-icon>Correo electrónico
                    </v-btn>
                    <a href="tel:2221903333">
                      <v-btn
                        class="btn-align-left"
                        block
                        :disabled="sending"
                        color="#13D8F2"
                        @click="send(), infoEmail()"
                      >
                        <v-icon right dark>phone</v-icon>Teléfono
                      </v-btn>
                    </a>
                  </div>

                  <div v-if="signUpStatus == 0">
                    <v-alert
                      v-model="alert"
                      dismissible
                      transition="scale-transition"
                      style="color: #13D8F2; background:#fff !important"
                    >{{ message }}</v-alert>
                    <div>
                      <v-btn
                        class="btn-align-left"
                        block
                        :disabled="sending"
                        color="#13D8F2"
                        @click="send(), shareEmail()"
                      >
                        <v-icon right dark>email</v-icon>Correo electrónico
                      </v-btn>
                      <a
                        :href="'whatsapp://send?text='+ shareText('whatsapp')"
                        data-action="share/whatsapp/share"
                      >
                        <v-btn
                          class="btn-align-left"
                          block
                          :disabled="sending"
                          color="green accent-4"
                          @click="send(), shareEmail()"
                        >
                          <v-icon right dark>phone</v-icon>WhatsApp
                        </v-btn>
                      </a>
                    </div>
                    <!-- <v-btn
                      class="btn-align-left"
                      block
                      :disabled="sending"
                      color="#13D8F2"
                      @click="send"
                    >
                      <v-icon right dark>share</v-icon>Compartir
                    </v-btn>-->
                  </div>

                  <div v-if="signUpStatus == 3">
                    <v-alert
                      v-model="alert"
                      dismissible
                      transition="scale-transition"
                      style="color: #13D8F2; background:#fff !important"
                    >{{ message }}</v-alert>
                    <div>
                      <v-btn
                        class="btn-align-left"
                        block
                        :disabled="sending"
                        color="#13D8F2"
                        @click="sendUrlEmail"
                      >
                        <v-icon right dark>email</v-icon>Correo electrónico
                      </v-btn>
                      <a
                        :href="'whatsapp://send?text='+ shareTextWp()"
                        data-action="share/whatsapp/share"
                      >
                        <v-btn
                          class="btn-align-left"
                          block
                          :disabled="sending"
                          color="green accent-4"
                          @click="save"
                        >
                          <v-icon right dark>phone</v-icon>WhatsApp
                        </v-btn>
                      </a>
                    </div>
                    <!-- <v-btn
                      class="btn-align-left"
                      block
                      :disabled="sending"
                      color="#13D8F2"
                      @click="send"
                    >
                      <v-icon right dark>share</v-icon>Compartir
                    </v-btn>-->
                  </div>

                  <v-btn class="btn-align-left" block color="#13D8F2" flat @click="closeDialog">
                    <!-- BOTON PARA CERRAR -->
                    <v-icon right dark>chevron_left</v-icon>Volver
                  </v-btn>
                </v-container>
              </v-card-text>
              <!-- <v-card-actions style="padding-right: 40px; padding-bottom:25px">
                <v-spacer></v-spacer>
                <v-btn color="#13D8F2" @click="closeDialog">Volver</v-btn>
                <v-btn :disabled="sending" color="#13D8F2" @click="send">Enviar</v-btn>
              </v-card-actions>-->
            </v-flex>
          </v-layout>
        </v-card>
      </v-dialog>
    </v-form>
    <Share :email="email" />
  </v-layout>
</template>

<script>
import Api from "@/services/api.js";
import Share from "./Share";
import { mapState, mapMutations } from "vuex";
export default {
  components: {
    Share
  },
  data() {
    return {
      // datos del usuario
      name: "",
      email: "",
      phone: "",
      password: "",
      messageInfo: "",
      message: "",
      alert: false,
      show: false,

      rules: {
        required: value => !!value || "Requerido.",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Correo no valido.";
        },
        number: value => {
          const pattern = /^([0-9])*$/;
          return pattern.test(value) || "Teléfono invalido.";
        }
      },

      // datos de envío

      sending: false,
      response: "",
      step: 0
    };
  },
  computed: {
    creditoPreautorizado() {
      if (this.tipoDeIngreso == "Asalariado") {
        return (parseFloat(this.ingresoMensual) / 2 - this.gastos) * 100;
      } else {
        return (parseFloat(this.ingresoMensual) * 0.36 - this.gastos) * 100;
      }
    },
    ...mapState([
      "tipoDeIngresoSeleccionado",
      "ingresoMensual",
      "backend",
      "tipoDePagoSeleccionado",
      "plazoSeleccionado",
      "cuantoCuentas",
      "tipoCredito",
      "valorDelInmueble",
      "signUp",
      "signUpStatus",
      "infoBancoSeleccionado",
      "gastos",
      "urlShareWhatsapp",
      "urlShareEmail"
    ])
  },
  methods: {
    shareText(platform) {
      let platforms = {
        whatsapp: `Tramita este crédito en https://creditohipotecarioexpress.com %0A%0ACrédito Hipotecario en *${this.infoBancoSeleccionado.nombre.toUpperCase()}* %0AMensualidad: *${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
        )}* %0AIngresos: *${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
        )}* %0ATasa fija anual: *${(
          this.infoBancoSeleccionado.valores.tasa * 100
        ).toFixed(
          2
        )} %* %0ACrédito Solicitado: *${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
        )}* %0ACrédito Pre autorizado: *${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}* %0A%0A `,
        email: `Tramita este crédito en https://creditohipotecarioexpress.com %0A%0ACrédito Hipotecario en ${this.infoBancoSeleccionado.nombre.toUpperCase()} %0A Mensualidad: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
        )} %0A Ingresos: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
        )} %0A Tasa fija anual: ${(
          this.infoBancoSeleccionado.valores.tasa * 100
        ).toFixed(2)} %0A Crédito Solicitado: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
        )} %0A Crédito Pre autorizado: ${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}`,
        share: `
        Tramita este crédito en https://creditohipotecarioexpress.com

        Crédito Hipotecario en ${this.infoBancoSeleccionado.nombre.toUpperCase()}

        Mensualidad: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
        )}
        Ingresos: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
        )}
        Tasa fija anual: ${(
          this.infoBancoSeleccionado.valores.tasa * 100
        ).toFixed(2)}
        Crédito Solicitado: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
        )}
        Crédito Pre autorizado: ${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}

        `
      };
      return platforms[platform];
    },
    shareTextWp() {
      return "*Compara este credito en los diferentes bancos:* " + this.urlShareWhatsapp;
    },
    send() {
      if (this.$refs.form.validate()) {
        this.sending = true;

        let data = {
          // datos del usuario
          name: this.name,
          email: this.email,
          phone1: this.phone,
          status: this.signUpStatus,
          message: this.messageInfo,
          type: 0,
          password: this.password,
          date: new Date(),

          // datos del credito

          monthlyPayment: this.infoBancoSeleccionado.valores.mensualidad,
          earnings: this.infoBancoSeleccionado.valores.ingresos,
          mortgageRate: this.infoBancoSeleccionado.valores.tasa,
          requestedCredit: this.infoBancoSeleccionado.valores.montoDeCredito,
          preAuthorizedCredit: this.creditoPreautorizado,
          propertyCost: this.valorDelInmueble,
          bank: this.infoBancoSeleccionado.nombre,
          paymentType: this.tipoDePagoSeleccionado,
          lapse: this.plazoSeleccionado,
          support: this.cuantoCuentas,
          supportType: this.tipoCredito
        };

        Api.post("transact/create.php", data).then(response => {
          console.log(response.data);
          if (this.signUpStatus == 1) {
            this.sending = false;
            this.alert = true;
            // this.closeDialog();
            this.message = response.data.message;
          } else if (this.signUpStatus == 0) {
            document.activeElement.blur();
            this.sending = false;
          }
        });
      }
    },

    shareEmail() {
      if (this.$refs.form.validate()) {
        this.sending = true;

        let data = {
          // datos del usuario
          email: this.email,
          name: this.name,
          phone: this.phone,
          message: this.messageInfo,
          // datos del credito

          monthlyPayment: this.$options.filters.currency(
            this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
          ),
          earnings: this.$options.filters.currency(
            this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
          ),
          mortgageRate:
            (this.infoBancoSeleccionado.valores.tasa * 100).toFixed(2) + " %",
          requestedCredit: this.$options.filters.currency(
            this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
          ),
          preAuthorizedCredit: this.$options.filters.currency(
            this.creditoPreautorizado.toFixed(2)
          ),
          propertyCost: this.$options.filters.currency(
            this.valorDelInmueble.toFixed(2)
          ),
          bank: this.infoBancoSeleccionado.nombre,
          paymentType: this.tipoDePagoSeleccionado,
          lapse: this.plazoSeleccionado,

          supportType: this.tipoCredito
        };

        Api.post("mail/share.php", data).then(response => {
          console.log(response.data);
          if (this.signUpStatus == 1) {
            this.sending = false;
            this.alert = true;
            // this.closeDialog();
            this.message = response.data.message;
          } else if (this.signUpStatus == 0) {
            document.activeElement.blur();
            this.alert = true;
            this.sending = false;
            this.message = response.data.message;
          }
        });
      }
    },
    infoEmail() {
      if (this.$refs.form.validate()) {
        this.sending = true;

        let data = {
          // datos del usuario
          email: this.email,
          name: this.name,
          phone: this.phone,
          message: this.messageInfo,

          // datos del credito

          monthlyPayment: this.$options.filters.currency(
            this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
          ),
          earnings: this.$options.filters.currency(
            this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
          ),
          mortgageRate:
            (this.infoBancoSeleccionado.valores.tasa * 100).toFixed(2) + " %",
          requestedCredit: this.$options.filters.currency(
            this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
          ),
          preAuthorizedCredit: this.$options.filters.currency(
            this.creditoPreautorizado.toFixed(2)
          ),
          propertyCost: this.$options.filters.currency(
            this.valorDelInmueble.toFixed(2)
          ),
          bank: this.infoBancoSeleccionado.nombre,
          paymentType: this.tipoDePagoSeleccionado,
          lapse: this.plazoSeleccionado,

          supportType: this.tipoCredito
        };

        Api.post("mail/info.php", data).then(response => {
          console.log(response.data);
          if (this.signUpStatus == 1) {
            this.sending = false;
            this.alert = true;
            // this.closeDialog();
            this.message = response.data.message;
          } else if (this.signUpStatus == 0) {
            document.activeElement.blur();
            this.sending = false;
          }
        });
      }
    },
    closeDialog() {
      this.updateSignUp(false);
    },
    ...mapMutations(["updateSignUp", "updateShare"]),

    save() {
      if (this.$refs.form.validate()) {
        this.sending = true;
        let data = {
          // datos del usuario
          name: this.name,
          email: this.email,
          phone1: this.phone,
          status: this.signUpStatus,
          message: this.messageInfo,
          type: 0,
          password: this.password,
          date: new Date()
        };
        Api.post("transact/create.php", data).then(response => {
          console.log(response.data);
        });
      }
    },

    sendUrlEmail() {
      if (this.$refs.form.validate()) {
        this.sending = true;

        let data = {
          phone: this.phone,
          email: this.email,
          name: this.name,
          url: this.urlShareEmail
        };

        console.log(data);

        Api.post("mail/shareUrl.php", data)
          .then(response => {
            console.log(response);
            this.message = response.data.message;
            this.alert = true;
            this.sending = false;
          })
          .catch(err => console.log(err));
      }
    }
  },
  watch: {
    signUp() {
      this.alert = false;
    }
  },
  mounted() {
    this.step = 1;
    this.$forceUpdate();
  }
};
</script>
