<template>
  <div>
    <v-layout>
      <v-dialog v-model="share" persistent max-width="300px">
        <v-card>
          <v-form ref="form">
            <v-card-text>
              <v-container>
                <v-layout wrap>
                  <v-flex xs12 class="center">
                    <a
                      :href="'whatsapp://send?text='+ shareText('whatsapp')"
                      data-action="share/whatsapp/share"
                    >
                      <v-btn fab dark large color="dark">
                        <img style="width:40px" :src="require('@/assets/logos/whatsapp-white.png')">
                      </v-btn>
                    </a>
                  </v-flex>
                  <v-flex xs12 class="center">
                    <v-btn fab dark large color="dark" @click="send">
                      <img style="width:40px" :src="require('@/assets/logos/mail-white.png')">
                    </v-btn>
                    <!-- <a
                      :href="'mailto:alguien@ejemplo.com?Subject=Simulador%20de%20crédito%20hipotecario%20express&body='+ shareText('email')"
                    >
                      <v-btn fab dark large color="dark">
                        <img style="width:40px" :src="require('@/assets/logos/mail-white.png')">
                      </v-btn>
                    </a>-->
                  </v-flex>

                  <!-- <v-flex xs12 class="center">
                    <a href="tel:2221903333">
                      <v-btn fab dark large color="dark">
                        <v-icon dark>phone</v-icon>
                      </v-btn>
                    </a>
                  </v-flex>-->
                  <!-- <v-flex xs12 class="center">
                    <v-btn @click="shareFunction" fab dark large color="blue">
                      <v-icon dark>share</v-icon>
                    </v-btn>
                  </v-flex>-->
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="updateShare(false)">Ok</v-btn>
            </v-card-actions>
            <div id="sharekeyboard"></div>
          </v-form>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: ["email"],
  computed: {
    creditoPreautorizado() {
      if (this.tipoDeIngreso == "Asalariado") {
        return (parseFloat(this.ingresoMensual) / 2 - this.gastos) * 100;
      } else {
        return (parseFloat(this.ingresoMensual) * 0.36 - this.gastos) * 100;
      }
    },
    ...mapState(["share", "infoBancoSeleccionado", "ingresoMensual", "gastos"])
  },
  methods: {
    shareText(platform) {
      let platforms = {
        whatsapp: `Tramita este crédito en https://creditohipotecarioexpress.com %0A%0ACrédito Hipotecario en *${this.infoBancoSeleccionado.nombre.toUpperCase()}* %0AMensualidad: *${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
        )}* %0AIngresos: *${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
        )}* %0ATasa fija anual: *${(
          this.infoBancoSeleccionado.valores.tasa * 100
        ).toFixed(
          2
        )} %* %0ACrédito Solicitado: *${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
        )}* %0ACrédito Pre autorizado: *${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}* %0A%0A `,
        email: `Tramita este crédito en https://creditohipotecarioexpress.com %0A%0ACrédito Hipotecario en ${this.infoBancoSeleccionado.nombre.toUpperCase()} %0A Mensualidad: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
        )} %0A Ingresos: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
        )} %0A Tasa fija anual: ${(
          this.infoBancoSeleccionado.valores.tasa * 100
        ).toFixed(2)} %0A Crédito Solicitado: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
        )} %0A Crédito Pre autorizado: ${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )}`,
        share: `
        Tramita este crédito en https://creditohipotecarioexpress.com

        Crédito Hipotecario en ${this.infoBancoSeleccionado.nombre.toUpperCase()} 
        
        Mensualidad: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.mensualidad.toFixed(2)
        )}
        Ingresos: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.ingresos.toFixed(2)
        )}
        Tasa fija anual: ${(
          this.infoBancoSeleccionado.valores.tasa * 100
        ).toFixed(2)} 
        Crédito Solicitado: ${this.$options.filters.currency(
          this.infoBancoSeleccionado.valores.montoDeCredito.toFixed(2)
        )} 
        Crédito Pre autorizado: ${this.$options.filters.currency(
          this.creditoPreautorizado.toFixed(2)
        )} 

        `
      };
      return platforms[platform];
    },
    shareFunction() {
      navigator.share({
        title: "Simulador de crédito hipotecario express",
        text: this.shareText("share")
      });
    },
    hideKeyboardStartTimeOut() {
      var field = document.createElement("input");
      field.setAttribute("type", "text");
      let inputContainer = document.getElementById("sharekeyboard");
      inputContainer.innerHTML = "";
      inputContainer.appendChild(field);
      inputContainer.style = "height:0px;width:100%";
      field.setAttribute("readonly", "readonly");

      // document.getElementById("toHideKeyboard").focus();
      field.focus();
      field.setAttribute("style", "display:none;width:100%");
    },
    send() {
      if (this.$refs.form.validate()) {
        this.sending = true;

        let data = {
          // datos del usuario
          name: this.name,
          email: this.email,
          phone1: this.phone,
          status: this.signUpStatus,
          message: this.message,
          type: 0,
          password: this.password,
          date: new Date(),

          // datos del credito

          monthlyPayment: this.infoBancoSeleccionado.valores.mensualidad,
          earnings: this.infoBancoSeleccionado.valores.ingresos,
          mortgageRate: this.infoBancoSeleccionado.valores.tasa,
          requestedCredit: this.infoBancoSeleccionado.valores.montoDeCredito,
          preAuthorizedCredit: this.creditoPreautorizado,
          propertyCost: this.valorDelInmueble,
          bank: this.infoBancoSeleccionado.nombre,
          paymentType: this.tipoDePagoSeleccionado,
          lapse: this.plazoSeleccionado,
          support: this.cuantoCuentas,
          supportType: this.tipoCredito
        };

        Api.post("transact/create.php", data).then(response => {
          console.log(response.data);
          if (this.signUpStatus == 1) {
            this.sending = false;
            this.alert = true;
            // this.closeDialog();
            this.response = response.data.message;
          } else if (this.signUpStatus == 0) {
            document.activeElement.blur();
            this.updateShare(true);
            this.sending = false;
          }
        });
      }
    },
    ...mapMutations(["updateShare"])
  },
  watch: {
    share() {
      this.hideKeyboardStartTimeOut();
    }
  }
};
</script>
