  <div class="upload-item">
    <label :for="'input' +index" class="w-100">
      <v-layout>
        <v-flex xs3 md2>
          <div v-if="statusFile == 0" class="file-button">
            <i class="el-icon-paperclip large"></i>
          </div>
          <div v-if="statusFile == 1" class="file-button blue">
            <i class="el-icon-loading large" style="color: white"></i>
          </div>
          <div v-if="statusFile == 2" class="file-button green">
            <i class="el-icon-upload large" style="color: white"></i>
          </div>
        </v-flex>
        <v-flex class="text" xs9 md10>
          <b>{{ fieldname }}</b>
          <br>
          <span class="filename" v-if="numFiles">
            <b>{{numFiles}}</b> archivo(s)
          </span>
        </v-flex>
      </v-layout>
    </label>

    <input
      :id="'input' + index"
      type="file"
      class="input-file-hidden"
      @change="pickFile"
      multiple
      accept="image/jpeg, image/gif, image/png, application/pdf"
    >
  </div>
