<script>
import Api from "@/services/api.js";
import { mapState } from "vuex";

export default {
  name: "inputFile",
  props: ["fieldname", "index"],

  data() {
    return {
      statusFile: 0,
      fileUpload: false,
      file: {},
      numFiles: 0
    };
  },
  computed: {
    ...mapState(["creditoTramitado"])
  },
  methods: {
    statusChange(status) {
      this.statusFile = status;
    },
    pickFile() {
      this.file = document.getElementById("input" + this.index).files;
      this.numFiles = Object.keys(this.file).length;
      this.filesUploded = 0;
      this.fileUpload = new Date();
    },
    sendFile(indexFile) {
      let data = {
        file: indexFile,
        field: this.fieldname,
        cid: this.creditoTramitado
      };

      this.statusChange(1);

      Api.file("files/uploads.php", data, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(response => {
          setTimeout(() => {
            if (this.filesUploded != this.numFiles) {
              this.filesUploded = this.filesUploded + 1;
              this.fileUpload = new Date();
            }
          }, 1000);
          this.statusChange(2);

        })
        .catch(e => {
          console.log(e);
        });
      this.statusChange(1);
    }
  },
  watch: {
    fileUpload() {
      this.sendFile(this.file[this.filesUploded]);
    }
  },
  mounted() {
    this.$emit("addInput");
  }
};
</script>

