<template>
  <div>
    <v-text-field
      class="no-margin"
      prefix="$"
      @blur="closeTimeout"
      @input="hideKeyboardStartTimeOut"
      v-model="number"
      label="Valor Del Inmueble"
      color="#13D8F2"
    ></v-text-field>
    <div id="valordelinmuebleinputkeyboard"></div>
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";

export default {
  mounted() {
    if (this.$route.query.inmueble !== null && this.$route.query.inmueble !== undefined) {
      this.number = this.$route.query.inmueble;
    }
  },

  data() {
    return {
      valorDelInmueble: "",
      number: ""
    };
  },
  computed: {
    hideKeyboardTime: {
      get() {
        return this.hideKeyboard;
      },
      set(value) {
        this.updateHideKeyboard(value);
      }
    },
    ...mapState(["hideKeyboard"])
  },
  methods: {
    closeTimeout() {
      let timeOut = this.hideKeyboardTime;
      clearTimeout(timeOut);
      this.updateHideKeyboard(timeOut);
    },
    onInput() {
      this.actualizarMontoDeCreditoPorModificacion(undefined);
      this.actualizarModificarMontoDeCredito(false);
    },
    currencyInput() {
      let number = parseFloat(this.number.replace(/,/g, "") || 0);
      this.valorDelInmueble = number;
      this.number = this.numberWithCommas(parseFloat(number));
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    hideKeyboardStartTimeOut() {
      var field = document.createElement("input");
      field.setAttribute("type", "text");
      let inputContainer = document.getElementById(
        "valordelinmuebleinputkeyboard"
      );
      inputContainer.innerHTML = "";
      inputContainer.appendChild(field);
      inputContainer.style = "height:0px;width:100%";
      field.setAttribute("readonly", "readonly");

      this.closeTimeout();
      this.hideKeyboardTime = setTimeout(() => {
        // document.getElementById("toHideKeyboard").focus();
        field.focus();
        field.setAttribute("style", "display:none;width:100%");
      }, 2000);
    },
    ...mapMutations([
      "actualizarValorDelInmueble",
      "actualizarModificarMontoDeCredito",
      "actualizarMontoDeCreditoPorModificacion",
      "updateHideKeyboard"
    ])
  },
  watch: {
    valorDelInmueble() {
      this.actualizarValorDelInmueble(this.valorDelInmueble);
      this.onInput();
    },
    number() {
      this.currencyInput();
    }
  }
};
</script>
